<template>
  <div>
    <div class="n_cont_cont"><div :style="style" class="n_cont"></div></div>
    <div class="den_reg">
      <div class="den_reg_casi">{{item.totale_casi}}</div>
      {{item.denominazione_regione}}
    </div>
  </div>
</template>

<script>
import gsap from 'gsap'

export default {
  name: 'Linea',
  props: ['item', 'maxcasi'],
  computed: {
      style () {
        var x = this.item.totale_casi / this.maxcasi * 100
        return 'height: ' + x + '%';
      }
    },
  mounted() {
    var t1 = gsap.timeline();
    t1.staggerTo('.n_cont', 1, {scaleY: 1, duration: 1, transformOrigin: 'bottom'}, 0.05)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.n_cont_cont {
  height: calc(90vh - 180px);
  position: relative;
}

.n_cont {
background-color: #0074D9;
position: absolute;
bottom: 0px;
transform: scaleY(0);
width: 100%;
}

.den_reg {
  font-size: 13px;
}

.den_reg_casi {
  line-height: 28px;
  text-align: center;
  margin-bottom: 10px;
  font-size: 13px;
  border: 1px solid #0074D9;
}

</style>
