<template>
    <div id="conti">
      <div><p>Totale Casi</p><p>{{contaTotale()}}</p></div>
      <div><p>Ospedalizzati</p><p>{{contaOsp()}}</p></div>
      <div><p>Decessi</p><p>{{contaDecessi()}}</p></div>
      <div><p>Guariti</p><p>{{contaGuariti()}}</p></div>
    </div>
</template>

<script>
export default {
    name: 'Totale',
    props: ['array'],
    methods: {
        contaTotale() {
        return this.array.reduce((a, {totale_casi}) => a + totale_casi, 0);
        },
        contaDecessi() {
        return this.array.reduce((a, {deceduti}) => a + deceduti, 0);
        },
        contaOsp() {
        return this.array.reduce((a, {totale_ospedalizzati}) => a + totale_ospedalizzati, 0);
        },
        contaGuariti() {
        return this.array.reduce((a, {dimessi_guariti}) => a + dimessi_guariti, 0);
        }
    }
}
</script>

