<template>
<div>
    <h1>Dati COVID-19 in Italia aggiornati al {{sortedJson()[0].data}}</h1>
      <div class="grid_reg">
      <Linea v-for="(item, index) in sortedJson()" :key="index" :item="item" :maxcasi="maxContagi()" />
    </div>
    <Table :array="sortedJsonMin()" />
    <Totale :array="sortedJsonMin()"  />
     <!-- {{parseData('https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_daily_reports/03-14-2020.csv', renderResults)}} -->
    <!--<div class="world_table">
      <h2>Dati COVID-19 dal mondo</h2>
      <div class="first_world_row"><div>Paese/Regione</div><div>Casi</div><div>Morti</div><div>Guariti</div></div>
      <World v-for="(item, index) in sliceArray()" :key="index" :world="item" />
    </div>-->
    <div class="footer_note">Visualizzazione dati COVID-19 | Ultimo aggiornamento: {{sortedJson()[0].data}}</div>
   
</div>
</template>

<script>
import Linea from './components/Linea.vue'
import axios from 'axios'
import Table from './components/Table.vue'
import Totale from './components/Totale.vue'
import Papa from 'papaparse'
//import World from './components/World.vue'

export default {
  name: 'App',
  components: {
    Linea,
    Table,
    Totale,
    //World
  },
  data () {
    return {
      info: '',
      parsed: ''
    }
  },
  mounted () {
    axios
      .get('https://raw.githubusercontent.com/pcm-dpc/COVID-19/master/dati-json/dpc-covid19-ita-regioni.json')
      .then(response => (this.info = response.data));
  },
  methods: {
    sliceArray() {
            return this.parsed.slice(0, 40);
    },
    sortedJson() {
      return this.info.slice(Math.max(this.info.length - 21, 0)).sort((t1,t2) => t1.totale_casi < t2.totale_casi ? -1 : 1);
    },
    sortedJsonMin() {
      return this.info.slice(Math.max(this.info.length - 21, 0)).sort((t1,t2) => t1.totale_casi > t2.totale_casi ? -1 : 1);
    },
    maxContagi() {
      let max = this.sortedJson()[0].totale_casi;
      for (let i = 1, len=this.sortedJson().length; i < len; i++) {
        let v = this.sortedJson()[i].totale_casi;
        max = (v > max) ? v : max;
      }
      return max  ;
    },
    renderResults(results) {
      this.parsed = results.data;
   },
    parseData(url, renderResults) {
      Papa.parse(url, {
          download: true,
          header: true,
          dynamicTyping: true,
          complete: function(results) {
            renderResults(results);
        }
        });
      }
  },
}
</script>

